.sendForm {
  position: fixed;
  bottom: 85px;
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: row;
  gap: 15px;
}

.textarea {
  outline: none;
  border: none;
  resize: none;
  border-radius: 20px;
  padding: 12px 24px;
  width: 975px;
  height: 40px;
  background-color: rgba(245, 247, 251, 1);
  filter: drop-shadow(0 1px 2 rgba(29, 33, 38, 0.1)) drop-shadow(0 5px 20 rgba(29, 33, 38, 0.03));
  color: rgba(0, 0, 0, 0.6);
  font-size: 14px;
  font-weight: 400;
}

.button {
  width: 120px;
  /* padding: 5px 40px; */
  border-radius: 20px;
  border: none;
  height: 40px;
  background-color: rgba(0, 102, 204, 1);
  outline: none;
  color: rgba(255, 255, 255, 1);
  font-weight: 600;
  font-size: 16px;
}

