.friendMessage {
  display: flex;
  flex-direction: column;
  gap: 5px;
  min-height: 77px;
  height: 77px;
  align-items: flex-end;
}

.friendImageAndText {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
  max-width: 415px;
}

.friendImageAndText img {
  height: 48px;
  width: 48px;
}

.friendText {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
  background-color: rgba(0, 102, 204, 1);
  border-radius: 5px 5px 0 5px;
  /* filter: drop-shadow(0 1px 2 rgba(29, 33, 38, 0.1)) drop-shadow(0 5px 20 rgba(29, 33, 38, 0.03)); */
  width: 415px;
  overflow-wrap: break-word;
  word-break: break-word;
  padding: 8px 13px;
  margin-right: 10px;
}

.friendText:after {
  content: '';
  bottom: 0;
  right: -10px;
  position: absolute;
  border-style: solid;
  border-width: 5px;
  border-color: transparent transparent   rgba(0, 102, 204, 1) rgba(0, 102, 204, 1);
}

.friendName {
  font-size: 16px;
  font-weight: 600;
  line-height: 19.5px;
  color: rgba(255, 255, 255, 1);
}

.friendMessageText {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  color: rgba(255, 255, 255, 1);
  white-space: pre-wrap;
  /* сохраняет пробелы и переносы строк */
  overflow-wrap: break-word;
  /* переносит длинные слова на новую строку */
  word-break: break-word;
  /* переносит длинные слова на новую строку (для старых браузеров) */
  hyphens: auto;
}

.friendTime {
  font-size: 10px;
  font-weight: 600;
  line-height: 12.19px;
  color: rgba(0, 0, 0, 1);
  margin-right: 10px;
}