.message {
  display: flex;
  flex-direction: column;
  gap: 5px;
  min-height: 77px;
  height: 77px;
  width: 465px;
  margin-bottom: 10px;

}

.imageAndText {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  max-width: 415px;

}

.imageAndText img {
  height: 48px;
  width: 48px;


}

.text {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 4px;
  background-color: rgba(245, 247, 251, 1);
  filter: drop-shadow(0 1px 2 rgba(29, 33, 38, 0.1)) drop-shadow(0 5px 20 rgba(29, 33, 38, 0.03));
  width: 415px;
  overflow-wrap: break-word;
  word-break: break-word;
  padding: 8px 13px;
  margin-left: 10px;
  border-radius: 5px 5px 5px 0;

}

.text:after {
  content: "";
  position: absolute;
  left: -10px;
  bottom: 0;
  border-style: solid;
  border-width: 5px 5px 5px 5px;
  border-color: transparent rgba(245, 247, 251, 1) rgba(245, 247, 251, 1) transparent;
  filter: drop-shadow(0 1px 2 rgba(29, 33, 38, 0.1)) drop-shadow(0 5px 20 rgba(29, 33, 38, 0.03));
}

.name {
  font-size: 16px;
  font-weight: 600;
  line-height: 19.5px;
  color: #000000;
}

.messageText {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  color: #000000;
  white-space: pre-wrap;
  /* сохраняет пробелы и переносы строк */
  overflow-wrap: break-word;
  /* переносит длинные слова на новую строку */
  word-break: break-word;
  /* переносит длинные слова на новую строку (для старых браузеров) */
  hyphens: auto;



}

.time {
  font-size: 10px;
  font-weight: 600;
  line-height: 12.19px;
  color: rgba(0, 0, 0, 1);
  margin-left: 10px;
}